<template>
  <div class="activityprint">
    <div>
      <v-row>
        <v-col cols="4"
          ><h2>{{ cData.company_name }}</h2></v-col
        >
        <v-col cols="4"
          ><h1 class="text-center">
            <strong>活動參與紀錄表</strong>
          </h1>
        </v-col>
        <v-col cols="4"></v-col>
      </v-row>
    </div>

    <v-divider></v-divider>

    <v-simple-table>
      <template v-slot:default>
        <tbody>
          <tr>
            <td class="title">活動名稱</td>
            <td colspan="3">
              {{ list.name }}
            </td>
          </tr>
          <tr>
            <td class="title">日期</td>
            <td colspan="3">
              <v-row>
                <!-- <v-col class="title">日期</v-col> -->
                <v-col>{{ list.date }}</v-col>
                <v-col class="title">時間</v-col>
                <v-col>{{ list.time }}</v-col>
              </v-row>
            </td>
            <!-- <td class="title">日期</td>
            <td>{{ list.date }}</td>
            <td class="title">時間</td>
            <td>{{ list.time }}</td> -->
          </tr>
          <tr>
            <td class="title">活動目標</td>
            <td colspan="3">
              {{ list.target }}
            </td>
          </tr>
          <tr>
            <td class="title">活動地點</td>
            <td colspan="3">
              {{ list.location }}
            </td>
          </tr>
          <tr>
            <td class="title">活動成員</td>
            <td colspan="3">
              <span v-if="list.memberArray">{{ members }}</span>
            </td>
          </tr>
          <tr>
            <td class="title">活動人數</td>
            <td colspan="3">
              <span v-if="list.memberArray">{{ userCounts }}</span>
            </td>
          </tr>
          <tr>
            <td class="title">器具設備</td>
            <td colspan="3">
              {{ list.device }}
            </td>
          </tr>
          <tr>
            <td class="title">帶領者</td>
            <td colspan="3">
              <v-row>
                <!-- <v-col class="title">帶領者</v-col> -->
                <v-col>{{ list.leader }}</v-col>
                <v-col class="title">協助者</v-col>
                <v-col>{{ list.helper }}</v-col>
              </v-row>
            </td>
            <!-- <td class="title">帶領者</td>
            <td>{{ list.leader }}</td>
            <td class="title">協助者</td>
            <td>{{ list.helper }}</td> -->
          </tr>

          <tr>
            <td colspan="4" class="text-center font-weight-bold grey lighten-3">
              <h2>活動時間表</h2>
            </td>
          </tr>
          <tr>
            <td colspan="2" class="title">活動時間</td>
            <td colspan="2" class="title">活動內容</td>
          </tr>
          <tr v-for="(schedule, index) in list.scheduleArray" :key="index">
            <td colspan="2">{{ schedule.time }}</td>
            <td colspan="2">{{ schedule.content }}</td>
          </tr>

          <tr>
            <td class="title">活動說明</td>
            <td colspan="3">{{ list.explanation }}</td>
          </tr>

          <tr>
            <td class="title">活動檢討與建議</td>
            <td colspan="3">
              {{ list.suggest }}
            </td>
          </tr>

          <tr>
            <td colspan="4" class="text-center font-weight-bold grey lighten-3">
              <h2>活動照片</h2>
            </td>
          </tr>

          <tr>
            <td colspan="4">
              <!-- <div class="d-flex justify-space-around">
                <img
                  v-for="(item, index) in picItems"
                  :key="index"
                  style="width: 33%"
                  :src="$store.state.base_url + '/upload/activity/' + item"
                />
              </div> -->
              <v-row>
                <v-col cols="4" v-for="(item, index) in list.picItems" :key="index">
                  <img :src="$store.state.base_url + '/upload/activity/' + item" style="width:100%">
                </v-col>
              </v-row>
            </td>
          </tr>
        </tbody>
      </template>
    </v-simple-table>

  </div>
</template>

<script>
export default {
  data() {
    return {
      cData: {},
      pData: {},
      uData: {},
      list: {}, //表單內資料
      picItems: [],
    };
  },

  computed: {
    // 參加成員
    members() {
      return this.list.memberArray.reduce((str, member, index) => {
        if (index != 0) {
          str += ",";
        }
        return (str += member.text);
      }, "");
    },
    userCounts() {
      //參加人數
      var man = this.list.memberArray.filter((item) => {
        return item.sex == "男";
      });
      var women = this.list.memberArray.filter((item) => {
        return item.sex == "女";
      });
      return (
        "總人數:" +
        this.list.memberArray.length +
        "人, 男:" +
        man.length +
        "人, 女:" +
        women.length +
        "人"
      );
    },
  },

  mounted() {
    this.cData = JSON.parse(sessionStorage.getItem("cData"));
    // this.pData = JSON.parse(sessionStorage.getItem("pData"));
    // this.uData = JSON.parse(sessionStorage.getItem("uData"));
    // this.list = JSON.parse(sessionStorage.getItem("printData"));
    this.list = JSON.parse(this.$route.query.data);
    this.getPic();
  },

  methods: {
    getPic() {
      //取得活動圖片
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getPicBySnkey/" +
            this.$store.state.databaseName +
            "/activity/activity/" +
            this.list.snkey
        )
        .then((rs) => {
          if (rs.data.state == 1) {
            this.picItems = rs.data.picArray;
          }
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.activityprint {
  .title {
    font-weight: bold;
    white-space: nowrap;
  }
  .v-data-table td {
    font-size: 1.2rem;
    height: 48px;
  }

  @media print {
    .title {
      font-weight: bold;
      white-space: nowrap;
    }
    .v-data-table td {
      font-size: 1.2rem;
      height: 48px;
    }
    @page {
      size: A4 portrait;
      margin: 0cm;
      padding: 0cm;
    }
  }
}
</style>